.app {
  // height: 100vh;

  .main {
    // width: 100%;
    height: calc(100vh - 70px);
    position: relative;
    top: 70px;
    overflow: scroll;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    > * {
      //   width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}
